*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  background-color: #fff;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding-block: 32px 16px;
  display: flex;
}

.card__product {
  display: flex;
}

.card__product-image {
  width: 160px;
  height: 134px;
}

.card__details {
  margin: 40px 0 0;
}

.card__details-name {
  color: #060b35;
  width: 166px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__details-code {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
}

.card__details-price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin: 24px 0 0;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.card__details-price span {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 0;
  display: flex;
}

.card__reviews-stars {
  align-items: center;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
}

.card__button-buy {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px 0 0;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__button-buy[data-qa="hover"]:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__reviews-stars--4 .star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
